.loading {
  margin: 20px 0;
  text-align: center;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  overflow: hidden;
}

.MuiGridList-root.grid-list {
  width: 100%;
  height: 100%;
  padding: 20px 10px;
}

.grid-tile {
  width: 100%;
  height: auto;
  font-weight: bold;
  min-height: 285px;
  cursor: pointer;
}

.grid-img {
  width: 100%;
}

.MuiGridListTileBar-title.grid-title {
  font-size: 18px;
}

.grid-subtitle {
  display: flex;
  justify-content: space-between;
}

.grid-vote-count {
  line-height: 24px;
  font-size: 14px;
  font-weight: normal;
}

.MuiGridListTileBar-rootSubtitle.grid-tile-bar {
  height: 100px;
  background-color: rgba(0, 0, 0, 0.7);
}
